      'http://img.mengdian.com/image/a1/24/4a/69/20170706/84b3aaaa3f6a0e51567c56573953de90.jpg',
      'http://img.mengdian.com/image/7b/1a/00/79/20170710/11187e5241d5291774023065dedfbf1a.jpg',
      'http://img.mengdian.com/image/f5/68/d1/36/20170704/26f2bbe40318fa5b57c274ee6443750c.jpg'
    ],
    swiperConfig: {
