<template>
  <div
    class="swiper-widget"
    :class="options.classname"
  >
    <div class="swiper-container" :id="options.id" v-show="showSwiper">
      <div class="swiper-wrapper">
        <div
          class="swiper-slide"
          v-for="(item, index) in options.lists"
          :class="{isCross:item.isCross}"
        >
          <img :src="item.image_url " alt="">
        </div>
      </div>
      <div class="swiper-pagination" v-if="options.swiperConfig && options.swiperConfig.showPagination"></div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  .swiper-slide {
    span {
      display: block;
      width: 100%;
      height: 300px;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
</style>
<script>
  import 'swiper/dist/css/swiper.css'
  import Swiper from 'swiper'

  export default {
    props: {
      options: {
        type: Object,
        default: function () {
          return {
            classname: 'swiper',
            id: 'swiper',
            lists: []
          }
        }
      }
    },
    data () {
      return {
        showSwiper: false,
        slider: undefined
      }
    },
    watch: {
      'options.lists': function (val) {
        this._init()
      }
    },
    mounted () {
    },
    methods: {
      _init () {
        const defaultConfig = {
          loop: false,
          speed: 800
        }
        let propsConfig = this.options.swiperConfig
        _.extend(defaultConfig, propsConfig)

        setTimeout(() => {
          this.slider = new Swiper('#' + this.options.id, defaultConfig)
        }, 50)
        this.showSwiper = true
      }
    }
  }
</script>



