<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <div class="page-content app-info" v-show="showPage">
      <div class="app-brief">
        <div class="app-img">
          <div>
            <span
            :style='"background-image: url(" + appInfo.icon + ")"'
            style="background-size: cover"
            ></span>
          </div>
          <div>
            <span v-html="appInfo.app_name"></span>
            <em v-html="appInfo.title"></em>
          </div>
          <div @click="goBack" class="icon-back"><img src="//qianka.b0.upaiyun.com/images/2c5733257df30858d93e4bc8a223b818.png" alt=""></div>
        </div>
        <div class="app-download">
          <div>
            <span>:</span>
            <span v-html="appInfo.intro"></span>
          </div>
          <div @click="goToAppstore(appInfo.app_id, appInfo.ct, appInfo.download_url)"><span></span><span v-text="appInfo.app_size"></span></div>
        </div>
      </div>
      <div
        class="app-img block-download"
        v-show="showDownloadBlock"
        :class="{'fixed':showDownloadBlock}"
      >
        <div>
            <span
              :style='"background-image: url(" + appInfo.icon + ")"'
              style="background-repeat: no-repeat;background-position: center center; background-size: cover"
            ></span>
        </div>
        <div>
          <span v-html="appInfo.app_name"></span>
          <em v-html="appInfo.title"></em>
        </div>
        <div
          class="btn-download"
          @click="goToAppstore(appInfo.app_id, appInfo.ct, appInfo.download_url)"
        ><span></span><span v-text="appInfo.app_size"></span></div>
      </div>
      <div class="app-swiper">
        <Swiper
          :options="options"
        ></Swiper>
      </div>
      <div class="app-text">
        <div class="title"></div>
        <p
          :style="{height: minHeight}"
          v-html="appInfo.info"
        >
        </p>
        <span
          @click.stop="viewMore"
          v-text="viewText"
        ></span>
      </div>
      <div class="app-detail">
        <div class="title"></div>
        <ul>
          <li v-if="appInfo.company_info && appInfo.company_info.fcom_name">
            <label></label>
            <p v-text="appInfo.company_info.fcom_name"></p>
          </li>
          <li v-if="appInfo.company_info && appInfo.company_info.com_type">
            <label></label>
            <p v-text="appInfo.company_info.com_type"></p>
          </li>
          <li v-if="appInfo && appInfo.app_size">
            <label></label>
            <p v-text="appInfo.app_size"></p>
          </li>
          <li v-if="appInfo.company_info && appInfo.company_info.level">
            <label></label>
            <p v-text="appInfo.company_info.level"></p>
          </li>
          <li v-if="appInfo.company_info && appInfo.company_info.latest_at">
            <label></label>
            <p v-text="appInfo.company_info.latest_at"></p>
          </li>
          <li v-if="appInfo.company_info && appInfo.company_info.language">
            <label></label>
            <p v-text="appInfo.company_info.language"></p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
  .page .app-info {
    padding: 30px 15px 10px;
    background: #fff;

    .app-brief {
      margin-bottom: 32px;
    }

    .app-img {
      display: flex;
      display: -webkit-flex;
      margin-bottom: 32px;
      position: relative;

      div:nth-child(1) {
        width: 80px;
        height: 80px;
        margin-right: 15px;

        span {
          display: block;
          padding-top: 100%;
          width: 100%;
          border-radius: 7px;
        }
      }

      div:nth-child(2) {
        display: flex;
        display: -webkit-flex;
        flex-direction: column;
        justify-content: space-around;
        width: 65%;
        padding: 10px 0;

        span {
          font-family: PingFangSC-Semibold;
          font-size: 16px;
          color: #3A3A3A;
          line-height: 16px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }

        em {
          font-family: PingFangSC-Regular;
          font-size: 12px;
          color: #B5B5B5;
          line-height: 12px;
        }
      }

      .icon-back {
        position: absolute;
        top: -10px;
        right: -10px;
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: flex-end;

        img {
          display: block;
          width: 20px;
          height: 20px;
          position: relative;
          right: 10px;
        }
      }
    }

    .block-download {
      padding: 20px 15px;

      div:nth-child(1) {
        width: 48px;
        height: 48px;
      }

      div:nth-child(2) {
        padding: 3px 0;
      }

      .btn-download {
        width: 52px;
        height: 52px;
        display: flex;
        -webkit-align-items: center;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        background: #4A90E2;
        border-radius: 4px;
        color: #fff;
        position: absolute;
        right: 15px;
        top: 50%;
        margin-top: -26px;

        span:nth-child(2) {
          font-size: 10px;
        }
      }
    }

    .fixed {
      position: fixed;
      top: 0px;
      left: 0px;
      width: 100%;
      background: #FDFDFD;
      z-index: 200;
    }

    .app-download {
      display: -webkit-flex;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      height: 60px;

      div:nth-child(1) {
        border-left: 5px solid #ddd;
        padding-left: 10px;
        width: 82%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        span {
          font-family: PingFangSC-Regular;
          font-size: 13px;
          color: #3A3A3A;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          word-break: break-all;
          max-height: 40px;
        }
      }

      div:nth-child(2) {
        display: -webkit-flex;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        width: 60px;
        height: 60px;
        background: #4A90E2;
        border-radius: 4px;
        color: #fff;

        span:first-child {
          font-size: 13px;
        }

        span:nth-child(2) {
          font-size: 10px;
        }
      }
    }

    .app-swiper {
      margin-bottom: 24px;
    }

    .app-text {
      margin-bottom: 32px;
      position: relative;

      .title {
        font-size: 20px;
        color: #B5B5B5;
        margin-bottom: 15px;
      }

      p {
        font-size: 13px;
        color: #3A3A3A;
        line-height: 25px;
        overflow: hidden;
      }

      span {
        position: absolute;
        right: 0px;
        bottom: 0px;
        color: #2887e0;
        font-size: 13px;
        width: 43px;
        height: 24px;
        line-height: 24px;
        text-align: right;
        background: #fff;
      }
    }

    .app-detail {
      .title {
        font-size: 20px;
        color: #B5B5B5;
        margin-bottom: 15px;
      }

      li {
        position: relative;
        padding-left: 60px;

        label {
          display: block;
          position: absolute;
          top: 0px;
          left: 0px;
          font-family: PingFangSC-Regular;
          font-size: 13px;
          color: #B5B5B5;
          line-height: 21px;
        }

        p {
          font-family: PingFangSC-Regular;
          font-size: 13px;
          color: #3A3A3A;
          line-height: 21px;
        }
      }
    }
  }
</style>
<script>
  import { hotapps_path } from 'constants'
  import Swiper from './Swiper.vue'
  import {appendUrlParams} from 'utils'

  export default {
    components: {
      Swiper
    },
    data () {
      return {
        isCrossNum: 0,
        showPage: false,
        timer: undefined,
        viewText: '',
        minHeight: '150px',
        showDownloadBlock: false,
        isShowMore: false,
        options: {
          classname: 'swiper',
          id: 'swiper',
          lists: [],
          swiperConfig: {
          }
        },
        appInfo: {}
      }
    },
    watch: {
      '$route.params': function (v) {
      }
    },
    created () {
      Vonic.app.pageContentScrollTop(0)
      let app_id = this.$route.params.app_id
      this.fetch(app_id)
    },
    mounted () {
      this.timer = setInterval(() => {
        let scrollTop = Vonic.app.pageContentScrollTop()
        let swiperOffsetTop = document.querySelector('.app-swiper').getBoundingClientRect().top
        let downloadOffsetTop = document.querySelector('.block-download').getBoundingClientRect().top
        if (scrollTop > swiperOffsetTop + downloadOffsetTop) {
          this.showDownloadBlock = true
        } else {
          this.showDownloadBlock = false
        }
      }, 50)
    },
    beforeDestroy () {
      clearInterval(this.timer)
    },
    methods: {
      fetch (app_id) {
        $api.getAppDetail(app_id)
        .then((res) => {
          this.showPage = true
          let data = res && res.data
          this.appInfo = data.payload
          let newImageList = []
          const reg = /\d{3}x\d{3}/
          this.appInfo.image_list.forEach((v, k) => {
            let image_size_a = reg.exec(v)
            let image_size = image_size_a[0]
            let newImage = image_size.split('x')
            const image_w = newImage[0]
            const image_h = newImage[1]
            let obj = {
              isCross: false,
              image_url: v
            }
            if (image_w < image_h) {
              this.isCrossNum += 1
              obj.isCross = true
            } else {
              obj.isCross = false
            }
            newImageList.push(obj)
          })
          if (this.isCrossNum < 2) {
            this.options.swiperConfig = {
              showPagination: false,
              loop: false,
              speed: 250,
              slidesPerView: 2,
              spaceBetween: 10,
              onSlideChangeStart () {
              }
            }
          }
          this.appInfo.image_list.splice(0, this.appInfo.image_list.length, newImageList)
          if (this.isCrossNum > 3) {
            newImageList.forEach((v, k) => {
              if (!v.isCross) {
                newImageList.splice(k, 1)
              }
            })
          }
          this.options.lists = this.appInfo.image_list[0]
        })
      },
      viewMore () {
        if (this.viewText === '') {
          this.minHeight = '150px'
          this.viewText = ''
        } else {
          this.minHeight = 'auto'
          this.viewText = ''
        }
      },
      goToAppstore (app_id, ct, url) {
        $api.installDownloadRecord(app_id, ct)
        location.href = appendUrlParams(url, {
          'return_url': location.href
        })
      },
      goBack () {
        let _path = $storage.get(hotapps_path)
        $router.back(_path)
      }
    }
  }
</script>



